@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap");
@font-face {
  font-family: "Futura";
  src: url("../font/FuturaBT-Book.woff2") format("woff2"), url("../font/FuturaBT-Book.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Futura";
  src: url("../font/FuturaCyrillic-Demi.woff2") format("woff2"), url("../font/FuturaCyrillic-Demi.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Futura";
  src: url("../font/FuturaBT-Medium.woff2") format("woff2"), url("../font/FuturaBT-Medium.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
:root {
  --pc: #DE3338;
  --sc:#667085;
  --body-color: #000;
  --gray-bg: #E5E7E9;
  --footer-bg: #272727;
  --white: #FFFFFF;
  --tf: transparent;
  --p-color: #999999;
  --h1toh6:#232323;
  --line-light: #D9D9D9;
  --btn-hover: red;
}

:root {
  --ani-2: all 0.2s 0s ease;
  --ani-3: all 0.3s 0s ease;
  --ani-4: all 0.4s 0s ease;
  --ani-5: all 0.5s 0s ease;
  --op-thr: 0.3;
  --op-n: 0.9;
  --op-e: 0.8;
  --op-f: 0.5;
  --font-t: 100;
  --font-el: 200;
  --font-l: 300;
  --font-r: 400;
  --font-m: 500;
  --font-sb: 600;
  --font-b: 700;
  --font-eb: 800;
  --font-black: 900;
  --font-eblack: 950;
}

body,
html {
  font-family: "Montserrate";
  line-height: 1.5;
  font-size: 16px;
  color: var(--body-color);
}

* {
  font-family: "Montserrate";
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
}

.h1,
h1,
.h2,
h2,
.h3,
h3,
.h4,
h4,
.h5,
h5,
.h6,
h6 {
  color: var(--h1toh6);
  line-height: 1.2;
}

.h1,
h1 {
  font-weight: var(--font-sb);
  font-size: 2.65rem;
}

.h2,
h2 {
  font-weight: var(--font-sb);
  font-size: 1.8rem;
}

.h3,
h3 {
  font-weight: var(--font-sb);
  font-size: 1.5rem;
}

.h4,
h4 {
  font-weight: var(--font-sb);
  font-size: 1.2rem;
}

.h5,
h5 {
  font-weight: var(--font-m);
  font-size: 1.05rem;
}

.h6,
h6 {
  font-weight: var(--font-b);
  font-size: 0.8rem;
}

p {
  color: var(--p-color);
  font-weight: var(--font-r);
}

a {
  color: var(--pc);
}
ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

svg {
  width: 100%;
  height: auto;
}

.btn {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  opacity: 1;
  border-width: 0.05rem;
  padding: 0.75rem 1.5rem;
  font-weight: var(--font-m);
  font-size: 1.1rem;
  line-height: 100%;
  letter-spacing: 0%;
  text-align: center;
  border-radius: 3rem;
}

.htwo {
  font-weight: var(--font-sb);
  font-size: 2.4rem;
  line-height: 100%;
}

.para {
  font-weight: var(--font-r);
  font-size: 1rem;
  line-height: 1.5rem;
  color: #232323;
}

