/* Variables */
.home {
  position: relative;
  padding-block: 3.05rem;
}
.home .bgimage {
  position: absolute;
  top: 5rem;
  inset: 0;
  background: url("../images/bg.webp") center/cover no-repeat;
  z-index: 0;
}
.home .bgimage .overlay {
  position: absolute;
  inset: 0;
  background: rgba(178, 34, 34, 0.6);
}
.home .container .subsection .content {
  position: relative;
  z-index: 2;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 7.5rem;
}
.home .container .subsection .content .div1 {
  max-width: 55%;
  color: #ffffff;
  display: flex;
  flex-direction: column;
}
.home .container .subsection .content .div1 .main-heading {
  max-width: 100%;
  color: #ffffff;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 3.2rem;
  line-height: 1;
  letter-spacing: 0;
  margin-bottom: 1rem;
}
.home .container .subsection .content .div1 .banner-para {
  font-size: 0.8rem;
  margin-bottom: 1.5rem;
  line-height: 1.6;
  color: #ffffff;
}
.home .container .subsection .content .div1 .btn {
  max-width: 9.1rem;
  height: 2.8rem;
  background: transparent;
  border: 0.05rem solid #D0D5DD;
  color: #ffffff;
  cursor: pointer;
}
.home .container .subsection .content .div2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 45%;
  background: #ffffff;
  border-radius: 1.2rem;
  padding-inline: 2.5rem;
  padding-block: 1.7rem 2.65rem;
}
.home .container .subsection .content .div2 .para1 {
  text-align: center;
  margin-bottom: 1rem;
  font-family: Montserrat;
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 2.2rem;
  letter-spacing: -2%;
  text-align: center;
  color: #101828;
}
.home .container .subsection .content .div2 .para2 {
  font-size: 0.7rem;
  margin-bottom: 1.5rem;
  color: #666;
  font-family: Montserrat;
  font-weight: 400;
  line-height: 1.5rem;
  font-weight: 400;
  font-size: 1rem;
  text-align: center;
}
.home .container .subsection .content .div2 #myform {
  display: flex;
  flex-direction: column;
}
.home .container .subsection .content .div2 #myform label {
  font-size: 0.6rem;
  color: #666;
}
.home .container .subsection .content .div2 #myform input {
  width: 100%;
  border: 0.05rem solid #ccc;
  border-radius: 0.25rem;
  opacity: 1;
  gap: 0.4rem;
  border-radius: 8px;
  border-width: 1px;
  padding: 0.6rem 0.8rem;
}
.home .container .subsection .content .div2 #myform .row {
  display: flex;
  gap: 1.6rem;
}
.home .container .subsection .content .div2 #myform .row .field {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin-bottom: 1.2rem;
  position: relative;
}
.home .container .subsection .content .div2 #myform .row .field .msg {
  border-color: red;
}
.home .container .subsection .content .div2 #myform .field {
  max-width: 100%;
  position: relative;
  margin-bottom: 1.2rem;
}
.home .container .subsection .content .div2 #myform small {
  visibility: hidden;
  position: relative;
}
.home .container .subsection .content .div2 #myform .error {
  visibility: visible;
  color: #b22222;
}
.home .container .subsection .content .div2 #myform .btn {
  background: var(--pc);
  color: #ffffff;
  border: none;
  padding: 0.6rem;
  border-radius: 1.5rem;
  cursor: pointer;
  margin-top: 0.4rem;
  font-weight: 500;
  font-style: Medium;
  font-size: 1.1rem;
  line-height: 100%;
  letter-spacing: 0%;
  height: 2.8rem;
}

.page1 {
  margin-block: 6rem;
}
.page1 .container {
  display: flex;
  flex-direction: column;
}
.page1 .container .page-heading {
  color: var(--pc);
  font-weight: 25rem;
  margin-bottom: 0.4rem;
  line-height: 100%;
  font-size: 0.8rem;
  opacity: 0.5;
}
.page1 .container .line {
  width: 100%;
  height: 0.1rem;
  border: 0.05rem solid #DE3338;
  margin-bottom: 2.5rem;
  opacity: 0.1;
}
.page1 .container .subsection {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 0.8rem;
  padding-top: 4rem;
  gap: 1.15rem;
}
.page1 .container .subsection .left {
  max-width: 50%;
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}
.page1 .container .subsection .left .about-heading {
  max-width: 90%;
}
.page1 .container .subsection .left .aboutsuryakiran-para {
  max-width: 80%;
  color: #232323;
}
.page1 .container .subsection .right {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  gap: 6rem;
  margin-block: 2.75rem;
  max-width: 50%;
}
.page1 .container .subsection .right .box {
  max-width: 9.65rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
}
.page1 .container .subsection .right .box .number {
  font-size: 3.2rem;
  font-weight: var(--font-b);
  color: black;
}
.page1 .container .subsection .right .box .small-heading {
  color: var(--pc);
  font-weight: var(--font-sb);
  font-size: 1.1rem;
  line-height: 100%;
}
.page1 .container .subsection .right .box:not(:last-child)::after {
  content: "";
  position: absolute;
  right: -3rem;
  top: 15%;
  height: 70%;
  width: 0.05rem;
  background-color: #ccc;
}

.header {
  height: 100%;
}
.header .container .logo-wrapper {
  height: 5rem;
}
.header .container .logo-wrapper img {
  width: 12.5rem;
  height: 100%;
  display: block;
  padding: 0.9rem;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes dash {
  from {
    stroke-dashoffset: 2000;
  }
  to {
    stroke-dashoffset: 0;
  }
}
@keyframes lineroad {
  to {
    stroke-dashoffset: 0;
  }
}
